<template>
  <div name="loginWindow" class="loginWindow-wrap">
    <div v-if="status">
      <div class="title">
        <font>登录</font>
      </div>
      <div class="form">
        <div style="margin: 40px 0 0 0">
          <div>
            <font>账号</font>
            <input
              type="text"
              placeholder="请输入您的账号"
              v-model="loginForm.account"
            />
          </div>
          <div style="margin: 20px 0 0 0">
            <font>密码</font>
            <input
              type="password"
              placeholder="请输入您的密码"
              v-model="loginForm.password"
            />
          </div>
        </div>
      </div>
      <div class="register" @click="status = !status">
        <font>还未注册？点击此处注册！</font>
      </div>
      <div class="submit" @click="login()">
        <div>
          <font>登录</font>
        </div>
      </div>
    </div>

    <div v-if="!status">
      <div class="title">
        <font>注册</font>
      </div>
      <div class="form">
        <div style="margin: 40px 0 0 0">
          <div>
            <font>用户名</font>
            <input
              type="text"
              placeholder="请填写您的用户名"
              v-model="registerForm.user_name"
            />
          </div>
          <div style="margin: 20px 0 0 0">
            <font>账号</font>
            <input
              type="text"
              placeholder="请填写您的账号"
              v-model="registerForm.user_account"
            />
          </div>
          <div style="margin: 20px 0 0 0">
            <font>密码</font>
            <input
              type="password"
              placeholder="请输入您的密码"
              v-model="registerForm.user_password"
            />
          </div>
        </div>
      </div>
      <div class="register" @click="status = !status">
        <font>返回</font>
      </div>
      <div class="submit" @click="register()">
        <div>
          <font>注册</font>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getUserlist,
  registerUser,
  getKeywordslist,
} from "@/api/homeAPI/articleslist";
export default {
  name: "loginWindow",
  data() {
    return {
      loginForm: {
        account: "",
        password: "",
      },
      registerForm: {
        user_id:"",
        user_name: "",
        user_head: "https://dummyimage.com/400X400", //默认头像
        user_background: "https://dummyimage.com/400X400", //默认背景
        user_major: "未知",
        user_college: "未知",
        user_sign: "请多多指教~！",
        user_introduce:"该用户暂未设置个人简介哦~",
        user_account: "",
        user_password: "",
      },
      user_list: [],
      status: true,
    };
  },
  methods: {
    login() {
      console.log(this.user_list[0]);
      if (!this.loginForm.account || !this.loginForm.password) {
        this.$notify({
          title: "账号/密码未填好！",
          type: "error",
        });
      } else {
        const users = {
          user_account: this.loginForm.account,
          user_password: this.loginForm.password,
        };
        let flag = 0;
        // 如果是新用户，将用户名+密码写入本地存储；如果是老用户，则判断密码是否正确！
        if (this.user_list.length != 0) {
          for (var i = 0; i < this.user_list.length; i++) {
            if (this.user_list[i].user_account == users.user_account) {
              flag = 1;
              // 进行判断
              if (this.user_list[i].user_password == users.user_password) {
                // 先获取用户相关信息，存入本地！
                localStorage.setItem(
                  "user_info",
                  JSON.stringify(this.user_list[i])
                );
                console.log("信息！", this.user_list[i]);
                getKeywordslist().then((res) => {
                  console.log(res.data[0]);
                  if (res.data) {
                    res.data.forEach((item) => {
                      if (item.id == this.user_list[i].id) {
                        console.log(res.data);
                        localStorage.setItem(
                          "keywords_list",
                          JSON.stringify(res.data.slice(1))
                        );
                      }
                    });
                  }
                });

                this.$router.push({ path: "/home" });
                this.$notify({
                  title: `${this.loginForm.account}`,
                  message: "欢迎回来！",
                  type: "success",
                });
              } else {
                this.$notify({
                  title: "密码输入错误，请重新输入！",
                  type: "error",
                });
              }
            }
          }
        } else {
          flag = 1;
          this.$notify({
            title: "检测到您输入的账号还未注册，请先进行注册！",
            type: "error",
          });
        }
        if (flag == 0) {
          this.$notify({
            title: "检测到您输入的账号还未注册，请先进行注册！",
            type: "error",
          });
        }
      }
    },
    register() {
      if (
        this.registerForm.user_name &&
        this.registerForm.user_account &&
        this.registerForm.user_password
      ) {
        registerUser(this.registerForm).then(
          (res) => {
            let flag = 0;
            this.user_list.forEach((item) => {
              if (item.user_account == this.registerForm.user_account) {
                flag = 1;
                this.$notify({
                  title: "该账户已经被他人注册啦！",
                  type: "error",
                });
              }
            });
            if (flag == 0) {
              console.log(res.data);
              this.$notify({
                title: "注册成功！",
                message: "快去进行登录吧！",
                type: "success",
              });
              // 初始化状态
              this.status = !this.status;
              this.loginForm = {
                account: "",
                password: "",
              };
              this.registerForm = {
                user_name: "",
                user_head: "https://dummyimage.com/400X400", //默认头像
                user_background: "https://dummyimage.com/400X400", //默认背景
                user_major: "未知",
                user_college: "未知",
                user_sign: "请多多指教~！",
                user_account: "",
                user_password: "",
              };
            } else {
              console.log(flag);
            }
          },
          (err) => {
            this.$notify({
              title: "注册失败！",
              message: `${err.data}`,
              type: "error",
            });
          }
        );
      } else {
        this.$notify({
          title: "注册必填项未填好！",
          type: "error",
        });
      }
    },
  },
  mounted() {
    getUserlist().then((res) => {
      console.log(res.data);
      if (res.data) {
        res.data.forEach((item) => {
          this.user_list.push(item);
        });
      }
    });
    console.log("132131233132", this.user_list);
  },
};
</script>

<style scoped>
.loginWindow-wrap {
  padding: 20px;
  margin: 200px auto;
  width: 300px;
  background-color: #fff;
  border-radius: 50px;
}
.title {
  width: 100%;
  display: flex;
  justify-content: center;
}
.title font {
  font-family: SourceHanSansCN-Medium;
  font-size: 24px;
  font-weight: 500;
  color: #3d3d3d;
}
.form {
  width: 100%;
  display: flex;
  justify-content: center;
}
.form font {
  font-family: SourceHanSansCN-Medium;
  font-size: 18px;
  font-weight: 500;
  line-height: 17px;
  color: #3d3d3d;
}
.form input {
  width: 200px;
  height: 40px;
  border-radius: 10px;
  border: 0.5px #ff8200 solid;
  background-color: #f5f5f5;
  margin: 0 0 0 20px;
}
.submit {
  width: 100%;
  margin: 30px 0 0 0;
  display: flex;
  justify-content: center;
}
.submit div {
  width: 70px;
  height: 40px;
  background-color: #ff820088;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.3s;
}
.submit div:hover {
  background-color: #ff8200;
}
.submit div font {
  font-family: SourceHanSansCN-Medium;
  font-size: 15px;
  font-weight: 500;
  color: #ffffff;
}
.register {
  width: 100%;
  margin: 10px 0 0 0;
  display: flex;
  justify-content: center;
  cursor: pointer;
}
.register font {
  font-family: SourceHanSansCN-Medium;
  font-size: 10px;
  font-weight: 500;
  line-height: 17px;
  color: #3d3d3d;
  transition: all 0.2s;
}
.register:hover font {
  color: #ff8200;
}
</style>